.container {
  padding: 0.1em;
  margin-top: 30px;
  width: 50%;
}

.container.grid {
  width: 40rem;
  height: 25rem;
  display: flex;
  justify-content: center;
}
.video {
  width: 90%;
  height: 100%;
  display: flex;
  align-items: center;
  cursor: pointer;
  transition: transform 250ms ease-in;
  color: aliceblue;
  padding: 5px;
  flex-direction: column;
}

.video:hover {
  transform: scale(1.05);
}

.thumbnail {
  width: 98%;
  height: 180px;
  margin-right: 5px;
}

.metadata {
  margin-left: 0.2em;
  display: block;
  width: 100%;
}

.channel {
  font-size: 1.4rem;
}

@media screen and (max-width: 980px) {
  .container.grid {
    width: 50%;
  }
  .thumbnail {
    height: 100%;
  }
}
@media screen and (max-width: 780px) {
  .container.grid {
    width: 100%;
  }
}
